import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      meta: {
        title: "首页",
        index: 1
      }
    }
  ]
})


